export default {
	data() {
		return {
			categoryList: null,
			fid: 160,
			mid: '',
			small: '',
			allList: {
				flag: true,
				productList: [],
				page: 1
			},
		}
	},
	onLoad() {
		this.getbig()
		this.height = uni.getSystemInfoSync().windowHeight;

		this.getList()
	},
	onReachBottom: function() {
		this.getgoodList()
	},
	methods: {
		// 获取分类商品
		getList() {
			let data = {
				fid: this.fid
			}
			this.$xm.post('/Procate/getMid', data, (res) => {
				this.tabbar = res;
				console.log(res)
				this.getgoodList()
			})

		},
		// 获取商品总分类
		getbig() {
			this.$xm.post('/Procate/getBig', '', (res) => {
				this.categoryList = res;
				console.log(res)
			})

		},
		// 获取商品
		getgoodList() {
			this.flag = false;
			let data = {
				big: this.fid,
				mid: this.mid,
				small: this.small,
				pageid: this.allList.page
			}
			this.$xm.post('/Product/proList', data, (res) => {
				let result = res.prolist;

				result.map((ele) => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
					ele.itemcount = 0;

				})

				let list = this.allList.productList.concat(result);

				let nextPage = ++this.allList.page;
				this.allList = {
					productList: list,
					page: nextPage,
				}
				console.log(this.allList.productList)
			})
		},
		showCategory(index) {
			this.loadingText = '正在加载中...';
			this.allList.productList = [];
			this.showCategoryIndex = index;
			this.fid = index;
			this.currentTab = 0;
			this.mid = '';
			this.allList.page = 1;
			this.getList()
		},
	}
}
